import React, { useState } from 'react';
import { Bar } from 'react-chartjs-2';
import 'chart.js/auto';
import { Search, Filter } from 'lucide-react';
import UserDetails from './UserDetails';

const usersData = [
  {
    id: '1',
    name: 'John',
    surname: 'Doe',
    email: 'john@example.com',
    phone: '123456789',
    phonePrefix: '+34',
    address: '123 Main St',
    city: 'Barcelona',
    subType: 'Premium',
    role: 'admin',
    stats: 5
  },
  {
    id: '2',
    name: 'Jane',
    surname: 'Smith',
    email: 'jane@example.com',
    phone: '987654321',
    phonePrefix: '+34',
    address: '456 Elm St',
    city: 'Madrid',
    subType: 'Standard',
    role: 'user',
    stats: 3
  },
  // Добавь больше пользователей по аналогии
];

const chartData = {
  labels: ['Jan', 'Feb', 'Mar', 'Apr'],
  datasets: [
    {
      label: 'Activity',
      data: [5, 6, 7, 8],
      backgroundColor: '#6b7280',
    },
  ],
};

export default function AdminMainPage() {
  const [selectedUser, setSelectedUser] = useState(null);
  const [users, setUsers] = useState(usersData);
  const [searchTerm, setSearchTerm] = useState('');
  const [showFilter, setShowFilter] = useState(false);
  const [roleFilter, setRoleFilter] = useState('');
  const [subTypeFilter, setSubTypeFilter] = useState('');

  const handleUserSelect = (user) => setSelectedUser(user);
  const handleSave = (editedUser) => {
    setUsers(users.map(u => u.id === editedUser.id ? editedUser : u));
    setSelectedUser(editedUser);
  };
  const handleDelete = (id) => {
    const updated = users.filter(u => u.id !== id);
    setUsers(updated);
    setSelectedUser(null);
  };

  const filteredUsers = users.filter(u =>
    (!roleFilter || u.role === roleFilter) &&
    (!subTypeFilter || u.subType === subTypeFilter) &&
    (`${u.name} ${u.surname}`.toLowerCase().includes(searchTerm.toLowerCase()))
  );

  return (
    <div className="flex h-screen overflow-hidden bg-[#ececec] p-6 gap-6">
      {/* Sidebar users */}
      <div className="w-1/4 flex flex-col">
        <div className="flex items-center gap-2 mb-2">
          <Search className="text-gray-600" />
          <input
            type="text"
            className="w-full px-2 py-1 rounded text-sm"
            placeholder="Search users..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <Filter
            className="text-gray-600 cursor-pointer"
            onClick={() => setShowFilter(!showFilter)}
          />
        </div>
        {showFilter && (
          <div className="bg-gray-300 text-sm rounded p-3 mb-2">
            <div className="mb-2">
              <div className="font-medium mb-1">Role</div>
              {['admin', 'user'].map(r => (
                <label key={r} className="block">
                  <input
                    type="radio"
                    name="role"
                    value={r}
                    checked={roleFilter === r}
                    onChange={() => setRoleFilter(r)}
                  /> {r}
                </label>
              ))}
            </div>
            <div>
              <div className="font-medium mb-1">Subscription</div>
              {['Standard', 'Premium', 'VIP'].map(t => (
                <label key={t} className="block">
                  <input
                    type="radio"
                    name="subType"
                    value={t}
                    checked={subTypeFilter === t}
                    onChange={() => setSubTypeFilter(t)}
                  /> {t}
                </label>
              ))}
            </div>
          </div>
        )}
        <div className="overflow-y-auto flex-1 bg-white rounded p-2 shadow-inner">
          {filteredUsers.map(user => (
            <div
              key={user.id}
              className="cursor-pointer p-2 border-b hover:bg-gray-200"
              onClick={() => handleUserSelect(user)}
            >
              {user.name} {user.surname}
            </div>
          ))}
        </div>
      </div>

      {/* Right content */}
      <div className="w-3/4 flex flex-col gap-6">
        <div className="bg-white rounded shadow p-4">
          <Bar data={chartData} height={150} options={{ maintainAspectRatio: false }} />
        </div>
        <div className="flex-1">
          <UserDetails user={selectedUser} onSave={handleSave} onDelete={handleDelete} />
        </div>
      </div>
    </div>
  );
}

