// src/app/adminmain/UserDetails.jsx
'use client';

import React, { useState, useEffect } from 'react';
import { Pencil, Trash2, Check } from 'lucide-react';

export default function UserDetails({ user, onSave, onDelete }) {
  const [isEditing, setIsEditing] = useState(false);
  const [editedUser, setEditedUser] = useState(user || {});

  useEffect(() => {
    setEditedUser(user || {});
  }, [user]);

  if (!user) {
    return <div className="text-center text-gray-500 py-10">Select a user to view details</div>;
  }

  const handleChange = (field, value) => {
    setEditedUser(prev => ({ ...prev, [field]: value }));
  };

  const handleSave = () => {
    setIsEditing(false);
    if (onSave) onSave(editedUser);
  };

  return (
    <div className="bg-[#f1eeea] rounded-2xl p-6 w-full flex flex-col gap-6 shadow-md">
      <div className="flex justify-between items-start">
        <div className="flex gap-4">
          <div className="bg-gray-400 w-28 h-28 rounded-full flex-shrink-0" />
        </div>
        <div className="flex gap-3 text-gray-700 items-center">
          <span className="bg-gray-300 w-6 h-6 rounded-full text-sm flex items-center justify-center">
            {user.stats || 3}
          </span>
          {isEditing ? (
            <Check className="cursor-pointer" onClick={handleSave} />
          ) : (
            <Pencil className="cursor-pointer" onClick={() => setIsEditing(true)} />
          )}
          <Trash2 className="cursor-pointer" onClick={() => onDelete(user.id)} />
        </div>
      </div>

      <div className="grid grid-cols-2 gap-x-8 gap-y-4">
        <div>
          <div className="text-gray-600 text-sm mb-1">Name</div>
          <input
            disabled={!isEditing}
            className="w-full rounded px-2 py-1 text-gray-700"
            value={editedUser.name || ''}
            onChange={e => handleChange('name', e.target.value)}
          />
        </div>
        <div>
          <div className="text-gray-600 text-sm mb-1">Surname</div>
          <input
            disabled={!isEditing}
            className="w-full rounded px-2 py-1 text-gray-700"
            value={editedUser.surname || ''}
            onChange={e => handleChange('surname', e.target.value)}
          />
        </div>
        <div>
          <div className="text-gray-600 text-sm mb-1">Email</div>
          <input
            disabled={!isEditing}
            className="w-full rounded px-2 py-1 text-gray-700"
            value={editedUser.email || ''}
            onChange={e => handleChange('email', e.target.value)}
          />
        </div>
        <div>
          <div className="text-gray-600 text-sm mb-1">Phone number</div>
          <div className="flex gap-1">
            <input
              disabled
              className="w-12 rounded px-2 py-1 bg-gray-200 text-gray-700"
              value={editedUser.phonePrefix || '+34'}
            />
            <input
              disabled={!isEditing}
              className="w-full rounded px-2 py-1 text-gray-700"
              value={editedUser.phone || ''}
              onChange={e => handleChange('phone', e.target.value)}
            />
          </div>
        </div>
        <div>
          <div className="text-gray-600 text-sm mb-1">ID</div>
          <input
            disabled
            className="w-full rounded px-2 py-1 bg-gray-200 text-gray-700"
            value={editedUser.id || ''}
          />
        </div>
        <div>
          <div className="text-gray-600 text-sm mb-1">Address</div>
          <input
            disabled={!isEditing}
            className="w-full rounded px-2 py-1 text-gray-700"
            value={editedUser.address || ''}
            onChange={e => handleChange('address', e.target.value)}
          />
        </div>
        <div>
          <div className="text-gray-600 text-sm mb-1">City</div>
          <input
            disabled={!isEditing}
            className="w-full rounded px-2 py-1 text-gray-700"
            value={editedUser.city || ''}
            onChange={e => handleChange('city', e.target.value)}
          />
        </div>
        <div>
          <div className="text-gray-600 text-sm mb-1">Sub type</div>
          <select
            disabled={!isEditing}
            className="w-full rounded px-2 py-1 text-gray-700"
            value={editedUser.subType || 'Standard'}
            onChange={e => handleChange('subType', e.target.value)}
          >
            <option value="Standard">Standard</option>
            <option value="Premium">Premium</option>
            <option value="VIP">VIP</option>
          </select>
        </div>
      </div>
    </div>
  );
}
